// Grid system
//
// Generate semantic grid columns with these mixins.

@mixin make-row(Rpgutter: Rpgrid-gutter-width) {
  --#{Rpprefix}gutter-x: #{Rpgutter};
  --#{Rpprefix}gutter-y: 0;
  display: flex;
  flex-wrap: wrap;
  // TODO: Revisit calc order after https://github.com/react-bootstrap/react-bootstrap/issues/6039 is fixed
  margin-top: calc(-1 * var(--#{Rpprefix}gutter-y)); // stylelint-disable-line function-disallowed-list
  margin-right: calc(-.5 * var(--#{Rpprefix}gutter-x)); // stylelint-disable-line function-disallowed-list
  margin-left: calc(-.5 * var(--#{Rpprefix}gutter-x)); // stylelint-disable-line function-disallowed-list
}

@mixin make-col-ready() {
  // Add box sizing if only the grid is loaded
  box-sizing: if(variable-exists(include-column-box-sizing) and Rpinclude-column-box-sizing, border-box, null);
  // Prevent columns from becoming too narrow when at smaller grid tiers by
  // always setting `width: 100%;`. This works because we set the width
  // later on to override this initial width.
  flex-shrink: 0;
  width: 100%;
  max-width: 100%; // Prevent `.col-auto`, `.col` (& responsive variants) from breaking out the grid
  padding-right: calc(var(--#{Rpprefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  padding-left: calc(var(--#{Rpprefix}gutter-x) * .5); // stylelint-disable-line function-disallowed-list
  margin-top: var(--#{Rpprefix}gutter-y);
}

@mixin make-col(Rpsize: false, Rpcolumns: Rpgrid-columns) {
  @if Rpsize {
    flex: 0 0 auto;
    width: percentage(divide(Rpsize, Rpcolumns));

  } @else {
    flex: 1 1 0;
    max-width: 100%;
  }
}

@mixin make-col-auto() {
  flex: 0 0 auto;
  width: auto;
}

@mixin make-col-offset(Rpsize, Rpcolumns: Rpgrid-columns) {
  Rpnum: divide(Rpsize, Rpcolumns);
  margin-left: if(Rpnum == 0, 0, percentage(Rpnum));
}

// Row columns
//
// Specify on a parent element(e.g., .row) to force immediate children into NN
// number of columns. Supports wrapping to new lines, but does not do a Masonry
// style grid.
@mixin row-cols(Rpcount) {
  > * {
    flex: 0 0 auto;
    width: divide(100%, Rpcount);
  }
}

// Framework grid generation
//
// Used only by Bootstrap to generate the correct number of grid classes given
// any value of `Rpgrid-columns`.

@mixin make-grid-columns(Rpcolumns: Rpgrid-columns, Rpgutter: Rpgrid-gutter-width, Rpbreakpoints: Rpgrid-breakpoints) {
  @each Rpbreakpoint in map-keys(Rpbreakpoints) {
    Rpinfix: breakpoint-infix(Rpbreakpoint, Rpbreakpoints);

    @include media-breakpoint-up(Rpbreakpoint, Rpbreakpoints) {
      // Provide basic `.col-{bp}` classes for equal-width flexbox columns
      .col#{Rpinfix} {
        flex: 1 0 0%; // Flexbugs #4: https://github.com/philipwalton/flexbugs#flexbug-4
      }

      .row-cols#{Rpinfix}-auto > * {
        @include make-col-auto();
      }

      @if Rpgrid-row-columns > 0 {
        @for Rpi from 1 through Rpgrid-row-columns {
          .row-cols#{Rpinfix}-#{Rpi} {
            @include row-cols(Rpi);
          }
        }
      }

      .col#{Rpinfix}-auto {
        @include make-col-auto();
      }

      @if Rpcolumns > 0 {
        @for Rpi from 1 through Rpcolumns {
          .col#{Rpinfix}-#{Rpi} {
            @include make-col(Rpi, Rpcolumns);
          }
        }

        // `Rpcolumns - 1` because offsetting by the width of an entire row isn't possible
        @for Rpi from 0 through (Rpcolumns - 1) {
          @if not (Rpinfix == "" and Rpi == 0) { // Avoid emitting useless .offset-0
            .offset#{Rpinfix}-#{Rpi} {
              @include make-col-offset(Rpi, Rpcolumns);
            }
          }
        }
      }

      // Gutters
      //
      // Make use of `.g-*`, `.gx-*` or `.gy-*` utilities to change spacing between the columns.
      @each Rpkey, Rpvalue in Rpgutters {
        .g#{Rpinfix}-#{Rpkey},
        .gx#{Rpinfix}-#{Rpkey} {
          --#{Rpprefix}gutter-x: #{Rpvalue};
        }

        .g#{Rpinfix}-#{Rpkey},
        .gy#{Rpinfix}-#{Rpkey} {
          --#{Rpprefix}gutter-y: #{Rpvalue};
        }
      }
    }
  }
}

@mixin make-cssgrid(Rpcolumns: Rpgrid-columns, Rpbreakpoints: Rpgrid-breakpoints) {
  @each Rpbreakpoint in map-keys(Rpbreakpoints) {
    Rpinfix: breakpoint-infix(Rpbreakpoint, Rpbreakpoints);

    @include media-breakpoint-up(Rpbreakpoint, Rpbreakpoints) {
      @if Rpcolumns > 0 {
        @for Rpi from 1 through Rpcolumns {
          .g-col#{Rpinfix}-#{Rpi} {
            grid-column: auto / span Rpi;
          }
        }

        // Start with `1` because `0` is and invalid value.
        // Ends with `Rpcolumns - 1` because offsetting by the width of an entire row isn't possible.
        @for Rpi from 1 through (Rpcolumns - 1) {
          .g-start#{Rpinfix}-#{Rpi} {
            grid-column-start: Rpi;
          }
        }
      }
    }
  }
}
