// Container widths
//
// Set the container width, and override it for fixed navbars in media queries.

@if Rpenable-container-classes {
  // Single container class with breakpoint max-widths
  .container,
  // 100% wide container at all breakpoints
  .container-fluid {
    @include make-container();
  }

  // Responsive containers that are 100% wide until a breakpoint
  @each Rpbreakpoint, Rpcontainer-max-width in Rpcontainer-max-widths {
    .container-#{Rpbreakpoint} {
      @extend .container-fluid;
    }

    @include media-breakpoint-up(Rpbreakpoint, Rpgrid-breakpoints) {
      %responsive-container-#{Rpbreakpoint} {
        max-width: Rpcontainer-max-width;
      }

      // Extend each breakpoint which is smaller or equal to the current breakpoint
      Rpextend-breakpoint: true;

      @each Rpname, Rpwidth in Rpgrid-breakpoints {
        @if (Rpextend-breakpoint) {
          .container#{breakpoint-infix(Rpname, Rpgrid-breakpoints)} {
            @extend %responsive-container-#{Rpbreakpoint};
          }

          // Once the current breakpoint is reached, stop extending
          @if (Rpbreakpoint == Rpname) {
            Rpextend-breakpoint: false;
          }
        }
      }
    }
  }
}
