// Row
//
// Rows contain your columns.

@if Rpenable-grid-classes {
  .row {
    @include make-row();

    > * {
      @include make-col-ready();
    }
  }
}

@if Rpenable-cssgrid {
  .grid {
    display: grid;
    grid-template-rows: repeat(var(--#{Rpprefix}rows, 1), 1fr);
    grid-template-columns: repeat(var(--#{Rpprefix}columns, #{Rpgrid-columns}), 1fr);
    gap: var(--#{Rpprefix}gap, #{Rpgrid-gutter-width});

    @include make-cssgrid();
  }
}


// Columns
//
// Common styles for small and large grid columns

@if Rpenable-grid-classes {
  @include make-grid-columns();
}
